.pricing {
    &-list {
        margin-bottom: 50px;
        padding: 10px;
    }
    &-box {
        width: 194px;
        margin: 0 auto;
        box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
        border-radius: 6px;
        background: #ffffff;
        padding: 27px;
    }
    &-title {
        text-align: center;
        font-weight: 300;
        font-size: 25px;
        margin-bottom: 40px;
    }
    &-info {
        font-weight: 300;
        font-size: 12px;
        text-align: center;
        margin-bottom: 46px;
        div {
            margin-bottom: 6px;
        }
    }
    &-numb {
        width: 132px;
        height: 132px;
        background: linear-gradient(180deg, #fede00 0%, #fecc2e 100%);
        border-radius: 50%;
        text-align: center;
        padding: 30px 0;
        &-text {
            font-size: 10px;
            font-weight: 300;
        }
        &-value {
            font-size: 38px;
            font-weight: 700;
            span {
                font-size: 27px;
                font-weight: 300;
            }
        }
    }
}
