.teachers {
    &-slider {
        margin-bottom: 50px;
    }
}

.teacher {
    &-sex {
        font-size: 20px;
        font-weight: 300;
    }
    &-name {
        font-size: 40px;
        font-weight: 600;
        white-space: nowrap;
        line-height: 29px;
        margin-bottom: 14px;
    }
    &-status {
        font-size: 15px;
        color: #7b7b7b;
    }
    &-skill {
        margin-bottom: 10px;
        &-icon {
            padding-right: 10px;
            width: 38px;
            text-align: center;
        }
        &-info {
            font-size: 12px;
        }
    }
}

.teachers-info-slider {
    pointer-events: none;
}

.teachers-slider {
    padding: 0 25px;
    .owl-item {
        transform: scale(0.9) !important;
        @include trs(0.2s, 0s);
        &.center {
            transform: scale(1) !important;
        }
    }

    .owl {
        &-prev {
            position: absolute;
            left: 5px;
            top: 50%;
            margin-top: -26px;
            cursor: pointer;
            span {
                display: block;
                width: 15px;
                height: 15px;
                border-left: 3px solid #000;
                border-top: 3px solid #000;
                transform: rotate(-45deg);
                font-size: 0;
            }
        }

        &-next {
            position: absolute;
            right: 5px;
            top: 50%;
            margin-top: -26px;
            cursor: pointer;
            span {
                display: block;
                width: 15px;
                height: 15px;
                border-right: 3px solid #000;
                border-top: 3px solid #000;
                transform: rotate(45deg);
                font-size: 0;
            }
        }
    }
}

/* Feel free to change duration  */
.animated {
    -webkit-animation-duration: 1000 ms;
    animation-duration: 1000 ms;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}
/* .owl-animated-out - only for current item */
/* This is very important class. Use z-index if you want move Out item above In item */
.owl-animated-out {
    z-index: 1;
}
/* .owl-animated-in - only for upcoming item
  /* This is very important class. Use z-index if you want move In item above Out item */
.owl-animated-in {
    z-index: 0;
}
/* .fadeOut is style taken from Animation.css and this is how it looks in owl.carousel.css:  */
.fadeOut {
    -webkit-animation-name: fadeOut;
    animation-name: fadeOut;
}
@-webkit-keyframes fadeOut {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}
@keyframes fadeOut {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}
