//buttons
.btn {
    border-radius: 43px;
    font-size: 25px;
    padding: 10px 18px;
    font-weight: 300;
    border: none;
    //sizes
    &-lg {
        padding: 20px 45px;
        font-size: 30px;
    }
    &-sm {
        font-size: 17px;
        padding: 6px 36px;
        border-radius: 10px;
        border-radius: 22px;
    }

    //colors
    &-warning {
        background: #ffea00; /* Old browsers */
        background: -moz-linear-gradient(top, #ffea00 0%, #e7b100 100%); /* FF3.6-15 */
        background: -webkit-linear-gradient(top, #ffea00 0%, #e7b100 100%); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(
            to bottom,
            #ffea00 0%,
            #e7b100 100%
        ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffea00', endColorstr='#e7b100',GradientType=0 ); /* IE6-9 */
    }
}

