//-- general styles
html,
body {
    height: 100%;
}

body {
    font: 20px 'Gilroy', sans-serif;
    color: #000000;
    background: #f7f7f9;
    position: relative;
}

h1,
.h1 {
}
h2,
.h2 {
    font-size: 40px;
    font-weight: 700;
}

.text {
    &-thin {
        font-weight: 300;
    }
}

@media (min-width: 1200px) {
    .container,
    .container-sm,
    .container-md,
    .container-lg,
    .container-xl {
        max-width: 980px;
    }
}

//-- common styles
* {
    outline-style: none;
    outline-width: 0px !important;
    outline-color: none !important;
}

//-- app styles

.section {
    padding: 70px 0;
    &-yellow {
        background: #ffcb30;
    }
    &-second {
        min-height: 100%;
        position: relative;
    }
}

.space {
    &-370 {
        height: 370px;
    }
    &-80 {
        height: 80px;
    }
}

.b-decor {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    min-height: 100%;
    z-index: -1;
    overflow: hidden;
    div {
        position: absolute;
    }
}

.decor {
    &-plane {
        left: 200px !important;
        top: 148px !important;
        background: url(../img/general/parralax/plane.png) no-repeat 0 0;
        width: 446px;
        height: 87px;
    }
    &-cloud {
        top: 200px !important;
        left: -50px !important;
        background: url(../img/general/parralax/cloud.png) no-repeat 0 0;
        width: 669px;
        height: 277px;
    }
    &-balloon {
        right: 120px !important;
        top: 300px !important;
        left: auto !important;
        background: url(../img/general/parralax/balloon.png) no-repeat 0 0;
        width: 155px;
        height: 195px;
    }
    &-cloud-2 {
        top: 505px !important;
        left: 70% !important;
        background: url(../img/general/parralax/cloud.png) no-repeat 0 0;
        width: 669px;
        height: 257px;
    }
}

.section {
    padding: 69px 0;
}

.btn-signin {
    position: relative;
    &:before {
        content: '';
        width: 136px;
        height: 53px;
        background: url(../img/general/paper-plane.png) no-repeat 0 0;
        position: absolute;
        top: -33px;
        right: 0;
    }
}

.b-web {
    &-header {
        background: #d9d9d9;
        border-radius: 13px 13px 0 0;
        padding: 6px 70px;
        position: relative;
        &-tools {
            width: 10px;
            height: 10px;
            background: #747474;
            border-radius: 50%;
            position: absolute;
            right: 36px;
            top: 50%;
            margin-top: -5px;
            &:before {
                content: '';
                width: 10px;
                height: 10px;
                background: #747474;
                border-radius: 50%;
                position: absolute;
                right: -20px;
                top: 0;
            }
            &:after {
                content: '';
                width: 10px;
                height: 10px;
                background: #747474;
                border-radius: 50%;
                position: absolute;
                left: -20px;
                top: 0;
            }
        }
        &-input {
            width: 70%;
            margin: 0 auto;
            background: #fff;
            border-radius: 16px;
            text-align: center;
            padding: 4px;
            font-size: 16px;
        }
    }
}

.b-web-slider {
    height: 536px;
    background: #fff;
}

.b-iphone {
    width: 282px;
    height: 572px;
    position: relative;
    padding: 10px 15px;
    overflow: hidden;
    background: #000;

    background: url(../img/general/iphone.png) no-repeat center center / contain;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    video {
        position: relative;
        z-index: -1;
        height: 100%;
    }
}

.section-second {
    position: relative;
}

.bg-intro {
    position: absolute !important;
    top: 0 !important;
    left: 0 !important;
    width: 100% !important;
    height: 100% !important;
    z-index: -2 !important;
    pointer-events: none;
    &-item {
        width: 100vw;
        height: 100%;
        &-yellow {
            background: #e7b100;
        }
        &-blue {
            background: #51dbe0;
        }
        &-green {
            background: #3addae;
        }
        &-greenlite {
            background: #bff943;
        }
    }
}

.modal-login {
    .modal-body {
        padding: 290px 13px 13px 13px;
        position: relative;
    }
    .modal-content {
        border-radius: 0 0 28px 28px;
    }
    .modal-dialog {
        margin-top: 0;
    }
}

.aist {
    width: 485px;
    height: 260px;
    background: url(../img/general/aist.png) no-repeat 0 0;
    position: absolute;
    top: 0;
    left: -98px;
}

.form-control {
    &-grey {
        background: #e9e9e9;
    }
    &-rounded {
        border-radius: 20px;
    }
}

.input-group {
    &-rounded {
        .form-control {
            border-radius: 20px;
            border: none;
        }
        .input-group-text {
            border-radius: 20px;
            border: none;
        }
    }
    &-grey {
        .input-group-text {
            background: #e9e9e9;
        }
        .form-control {
            background: #e9e9e9;
        }
    }
}
