  .intro-ibox-text .icon-headphones {
    display: none;
  }
  .btn-lg, .btn-group-lg > .btn {
    font-size: 18px;
    padding: 10px 15px;
  }
  .intro-text-slider-item {
    font-size: 30px;
  }
  .intro {
    height: 80vh;
    display: flex;
    align-items: center;
  }
  .intro-subtext {
    font-size: 18px;
  }
  h2, .h2 {
    font-size: 30px;
  }
  .b-web-slider {
    height: 418px;
  }
}

@media screen and (max-width: 767.98px) {
  body {
    font-size: 16px;
  }
  h2, .h2 {
    font-size: 20px;
  }
  .phone {
    margin: 0;
  }
  .intro-text-slider-item {
    font-size: 18px;
  }
  .intro-subtext {
    font-size: 16px;
  }
  .b-web-slider {
    height: 309px;
  }
}

@media screen and (max-width: 575.98px) {
  h2, .h2 {
    font-size: 18px;
  }
  .b-web-header {
    padding-left: 10px;
    padding-right: 75px;
  }
  .b-web-header-input {
    width: auto;
  }
}
