.intro {
    &-text-slider {
        &-item {
            font-size: 50px;
            font-weight: 600;
            margin-bottom: 20px;
            div {
                @include trs(0.5s, 0s);
                overflow: hidden;
            }
        }
    }

    &-subtext {
        font-size: 25px;
        font-weight: 600;
    }

    &-ibox {
        position: relative;
        width: 202px;
        margin: 0 auto;
        &-test {
            margin-bottom: 25px;
            position: relative;
            &:before {
                content: '';
                position: absolute;
                left: -38px;
                top: 26px;
                height: 65px;
                width: 65px;
                border-left: 1px solid #000;
                border-radius: 50%;
            }
            .btn {
                font-size: 20px;
                padding: 9px 10px;
                width: 202px;
                &-warning {
                    background: linear-gradient(180deg, #ffea00 0%, rgba(255, 255, 255, 0) 100%), #e7b100;
                }
            }
        }
        &-text {
            position: relative;
            padding-left: 40px;
            font-size: 10px;
            font-weight: 300;
            line-height: 10px;
            .icon-headphones {
                position: absolute;
                left: 0;
                top: 50%;
                transform: translateY(-50%);
            }
        }
    }
}
